.services-section {
  background-color: #f8f9fa;
  padding: 100px 0;
}

.section-header {
  text-align: center;
  margin-bottom: 80px;
}

.services-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 40px;
  max-width: 1200px;
  margin: 0 auto;
}

.service-card {
  background: white;
  border-radius: 15px;
  padding: 40px 30px;
  text-align: center;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}

.service-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: linear-gradient(90deg, #1366d6, #0052cc);
  transform: scaleX(0);
  transition: transform 0.3s ease;
}

.service-card:hover {
  transform: translateY(-10px);
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.15);
}

.service-card:hover::before {
  transform: scaleX(1);
}

.service-icon {
  width: 80px;
  height: 80px;
  margin: 0 auto 30px;
  background: linear-gradient(135deg, #1366d6, #0052cc);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 2.5rem;
  box-shadow: 0 10px 20px rgba(19, 102, 214, 0.3);
  transition: all 0.3s ease;
}

.service-card:hover .service-icon {
  transform: scale(1.1);
  box-shadow: 0 15px 30px rgba(19, 102, 214, 0.4);
}

.service-title {
  font-size: 1.5rem;
  font-weight: 700;
  color: #333;
  margin-bottom: 20px;
  text-decoration: underline;
  text-decoration-color: #ffc107;
  text-decoration-thickness: 3px;
  text-underline-offset: 5px;
}

.service-description {
  color: #666;
  line-height: 1.7;
  font-size: 1rem;
}

/* Responsive Design */
@media (max-width: 768px) {
  .services-section {
    padding: 80px 0;
  }

  .section-header {
    margin-bottom: 60px;
  }

  .services-grid {
    grid-template-columns: 1fr;
    gap: 30px;
  }

  .service-card {
    padding: 30px 20px;
  }

  .service-icon {
    width: 70px;
    height: 70px;
    font-size: 2rem;
  }

  .service-title {
    font-size: 1.3rem;
  }

  .service-description {
    font-size: 0.95rem;
  }
}

@media (max-width: 480px) {
  .services-section {
    padding: 60px 0;
  }

  .service-card {
    padding: 25px 15px;
  }

  .service-icon {
    width: 60px;
    height: 60px;
    font-size: 1.8rem;
    margin-bottom: 20px;
  }

  .service-title {
    font-size: 1.2rem;
  }
}